<template>
	<div id="blogpost-page">
		<div class="blogpost container">
			<div class="blogpost-wrapper mt-4">
				<h3 class="text-center">{{post.title}}</h3>
				<p class="text-center">
					<small><i class="fa fa-clock mr-1"></i> <timeago :datetime="new Date(post.datetime)"></timeago></small>
				</p>
				
				<vue-markdown :source="post.content" class="vue-markdown">
				</vue-markdown>
				<div v-if="is_loaded" class="blogpost-footer row mt-5">
					<div class="col-md-6">
						<router-link v-if="pagination.before" :to="{ name: 'BlogPost', params: { slug: pagination.before.slug } }">
							<i class="fas fa-chevron-left mr-2"></i>{{pagination.before.title}}
						</router-link>
						<span v-else>This is first post</span>
					</div>
					<div class="col-md-6 text-right">
						<router-link v-if="pagination.next" :to="{ name: 'BlogPost', params: { slug: pagination.next.slug } }">
							{{pagination.next.title}}<i class="fas fa-chevron-right ml-2"></i>
						</router-link>
						<span v-else>This is last post</span>
					</div>
				</div>

				
			</div>
		</div>

		<p class="text-center my-4">
			<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
		</p>
		<ButtonToTop />
		<div class="pb-5"></div>
	</div>
</template>

<script>
import axios from 'axios'
import firebase from './../services/firebase'
import viewCounter from './../mixins/viewCounter'
import VueMarkdown from 'vue-markdown'
import ButtonToTop from '../components/ButtonToTop'
import '../assets/css/blogpost.css'
export default {
	data(){
		return {
			blog_data: [],
			post: {},
			pagination: {
				before: {},
				next: {}
			},
			is_loaded: false
		}
	},
	mixins: [viewCounter],
	components: { VueMarkdown, ButtonToTop },
	methods: {
		showPost(){
			const db = firebase.firestore()
			db.collection('blog')
			.get()
			.then((q) => {
				let data = []
				q.forEach((doc) => {
					data.push(doc.data())
				})
				this.blog_data = data

				this.blog_data.sort((a, b)=>{
					let dateA = new Date(a.datetime.toDate().toString())
					let dateB = new Date(b.datetime.toDate().toString())
					return dateA - dateB
				})

				let slug = this.$route.params.slug
				let show_post

				this.blog_data.forEach((e) => {
					if(slug == e.slug){
						show_post = e
						show_post.datetime = e.datetime.toDate().toString()

						// get md file post content
						axios
						.get('/blog/'+e.file)
						.then(res => {
							show_post.content = JSON.parse(JSON.stringify(res.data))
							this.post = show_post
							this.setPagination()
						})
					}
				})
			})
			.catch((error) => {
				console.error("Error getting documents: ", error)
			})
		},
		setPagination(){
			let post = this.post
			let i = 0
			let cur_index = 0 // current post index
			this.blog_data.forEach(function(e){
				// get current index
				if(post.slug == e.slug){
					cur_index = i
				}
				i += 1
			})
			this.pagination.before = this.blog_data[cur_index-1]
			this.pagination.next = this.blog_data[cur_index+1]
			this.is_loaded = true;
		}
	},
	mounted(){
		this.updatePageview() // call mixins
		this.showPost()
	}
}
</script>